import { Pipe,PipeTransform } from '@angular/core' ;
import { FlxUiDatatableService } from '../flx-ui-datatable.service';
@Pipe({
    name:'limittext'
})
export class LimitText implements PipeTransform{
    constructor(public service:FlxUiDatatableService){

    }

    transform(mainpulationConfig:Object,text:string):string{
        let from = (mainpulationConfig.hasOwnProperty("from")) ? mainpulationConfig['from'] : 0 ;
        let to = (mainpulationConfig.hasOwnProperty("to")) ? mainpulationConfig["to"] : text.length ;
        let appendDots = (mainpulationConfig.hasOwnProperty("appendDots")) ? mainpulationConfig["appendDots"] : "" ;
        let txt: string ="" ;
        if(text.length>to){
            txt = text.substring(from,to)+ appendDots ;
        }else{
            txt = text ;
        }

        return txt ;
    }
}