import { Pipe, PipeTransform } from '@angular/core' ;
import { FlxUiDatatableService } from '../flx-ui-datatable.service';

@Pipe({
    name:'haslimittext'
})
export class HasLimitText implements PipeTransform{
    constructor(public service: FlxUiDatatableService){}

    transform(array:Array<number>,indexReached:number): any{
       let found: boolean ;
       for(let i of array){
           if(i==indexReached){
                found = true ;
                break ;
           }
       }

       return found ;
    }
}