import { Pipe,PipeTransform } from '@angular/core' ;
import { FlxUiDatatableService } from '../flx-ui-datatable.service';

@Pipe({
    name:'isprocessed'
})
export class ProcessedData implements PipeTransform{
    constructor(public service: FlxUiDatatableService){

    }

    transform(dataIndex:number): boolean{
        let isProcessed: boolean = false ;
        let foundIndex: number = -1 ;
        for(let i=0;i<this.service.processedData.length;i++){
            if(i==dataIndex){
                isProcessed = true ;
                foundIndex = i ;
            }
        }

        if(foundIndex<0){            
            this.service.processedData.push(dataIndex) ;
        }

        return isProcessed ;
    }
}